<template>
  <div class="user">
    <div class="user-avatar">
      <img
        :src="`/img/avatars/${user.avatar}`"
        :alt="`${user.username} avatar`"
      />
    </div>
    <div class="user-info">
      <div class="user-info-name">{{ user.username }}</div>
      <div class="user-info-stats">
        <p>Rank: {{ user.season.seasonRank }}</p>
        <p>
          Ratio: {{ user.season.wins }} - {{ user.season.losses }} ({{
            user.season.streak
          }})
        </p>
      </div>
    </div>

    <div class="user-charlist">
      <img
        v-for="(char, key) in charList"
        :key="key"
        :src="`/uploads/${char.facepic}.jpg`"
      />
    </div>
  </div>
</template>

<script>
export default {
  props: ["user"],
  computed: {
    charList() {
      const arr = [];
      for (const char of this.$store.getters["game/getMyCharList"](
        this.user.id
      )) {
        if (char) arr.push(char);
      }
      return arr;
    },
  },
};
</script>

